.navbar {
  height: 45px;
  width: 100%;
  color: #fff;
  background-color: #20a0ff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 10px;
}
.navbar .fa {
    font-size: 18px;
}
.navbar a.navbar-brand {
    text-decoration: none;
    color: #fff;
}
.navbar .navbar-brand {
    display: inline-block;
    width: 185px;
}
.navbar .navbar-brand img {
      border: 0;
}
.navbar .navbar-brand img, .navbar .navbar-brand span {
      vertical-align: middle;
      line-height: 45px;
}
.navbar .navbar-brand:after {
  content: "";
  line-height: 45px;
}
.navbar .navbar-brand.collapse {
  width: 44px;
}
.navbar .navbar-brand.collapse span {
        display: none;
}
.navbar .sidebar-collapse {
    float: left;
    vertical-align: middle;
    line-height: 45px;
    width: 35px;
    text-align: center;
    cursor: pointer
}
.navbar .sidebar-collapse.active, .navbar .sidebar-collapse:hover {
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, .2);
          box-shadow: 0 0 20px rgba(0, 0, 0, .2);
}
@media (max-width: 768px) {
.navbar {
    padding-left: 0;
}
.navbar .navbar-brand {
      display: none;
}
.navbar .sidebar-collapse {
      width: 54px;
}
}
.navbar .account-area {
    float: right;
    height: 100%;
    margin-right: -15px;
    padding-right: 15px;
}
.navbar .account-area .heartbit {
      position: relative;
}
.navbar .account-area .heartbit .fa {
        font-size: 14px;
}
.navbar .account-area .heartbit:before {
      content: "";
      width: 5px;
      height: 5px;
      background: #ff4949;
      border-radius: 5px;
      position: absolute;
      top: 0;
      right: 0;
}
.navbar .account-area .heartbit:after {
      content: "";
      width: 21px;
      height: 21px;
      width: 0\9;
      height: 0\9;
      background: #ff4949;
      border-radius: 21px;
      position: absolute;
      top: -8px;
      right: -8px;
      -webkit-animation: heartbit 1.5s infinite ease-out;
              animation: heartbit 1.5s infinite ease-out;
      -webkit-transform: scale(0);
          -ms-transform: scale(0);
              transform: scale(0);
}
.navbar .account-area .fa-user-o {
      display: none;
}
.navbar .account-area .avatar {
      height: 35px;
      border-radius: 50%;
}
.navbar .account-area .avatar, .navbar .account-area .username, .navbar .account-area .fa {
      line-height: 45px;
      vertical-align: middle;
}
.navbar .account-area .el-dropdown-link {
      color: #fff;
      cursor: pointer;
      display: inline-block;
      height: 45px;
      padding: 0 15px
}
.navbar .account-area .el-dropdown-link:hover {
  background-color: rgb(33, 153, 245);
}
@media (max-width: 768px) {
.navbar .account-area .avatar, .navbar .account-area .username, .navbar .account-area .fa-caret-down {
        display: none;
}
.navbar .account-area .fa-user-o {
        display: inline-block;
}
}
.navbar-fixed-top {
  position: fixed;
  z-index: 1000;
}
.notification {
  width: 250px;
  max-height: 310px
}
.notification.el-dropdown-menu {
  padding: 0;
  margin: 0;
}
.notification .el-dropdown-menu__item {
    color: #333;
    line-height: 1.42857;
    font-size: 14px;
    font-weight: normal;
    padding: 10px;
}
.notification .el-dropdown-menu__item:hover {
  color: #333;
}
.notification .el-dropdown-menu__item.el-dropdown-menu__item--divided {
  margin: 0;
  padding: 0 10px;
}
.notification .el-dropdown-menu__item.el-dropdown-menu__item--divided:before {
  height: 0;
}
.notification .el-dropdown-menu__item.title {
  background-color: rgb(228, 232, 241);
}
.notification .el-dropdown-menu__item.more {
  color: #20a0ff;
  text-align: center;
  background-color: rgb(228, 232, 241);
}
.notification .el-dropdown-menu__item.more i {
        margin-left: 5px;
}
.notification h5 {
    font-size: 14px;
    margin: 0;
    font-weight: normal;
}
.notification p {
    margin: 0;
}
@-webkit-keyframes heartbit {
0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
25% {
    opacity: 0.4;
}
100% {
    opacity: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes heartbit {
0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
}
25% {
    opacity: 0.4;
}
100% {
    opacity: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
}
}



