.app {
  min-height: 100%;
  height: auto;
  position: relative;
  overflow: hidden
}
.app:after {
  content: "";
  display: table;
  clear: both;
}
.app .sidebar.collapse ~ .container {
    margin-left: 54px
}
@media (max-width: 768px) {
.app .sidebar.collapse ~ .container {
    margin-left: 0;
}
}
.app .container {
    margin-left: 230px;
    margin-top: 45px;
    overflow: hidden;
}
.app .page-container {
    margin: 15px 15px;
}



