.app .sidebar {
    float: left;
    top: 45px;
    bottom: 0;
    width: 230px;
    background-color: #fff;
    overflow: visible;
}
@media (max-width: 768px) {
.app .sidebar.collapse {
    display: none;
}
}
.app .sidebar.sidebar-fixed {
  position: fixed;
  z-index: 999;
}
.app .sidebar:before {
  content: "";
  position: fixed;
  top: 0;
  bottom: 0;
  width: 230px;
  background-color: #fff;
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, .2);
          box-shadow: 0 0 20px rgba(0, 0, 0, .2);
}
.app .sidebar.collapse, .app .sidebar.collapse:before {
  width: 54px;
}
.app .sidebar.collapse > .el-menu .el-submenu .el-menu {
  position: fixed;
  top: 45px;
  left: 54px;
}
.app .sidebar:not(.collapse) > .el-menu .el-submenu .el-menu {
        top: 0 !important;
        bottom: auto !important;
        bottom: initial !important;
}
.app .sidebar .el-menu-item .fa, .app .sidebar .el-submenu .fa {
      vertical-align: baseline;
      margin-right: 10px;
}
.app .sidebar > .el-menu {
      background-color: #fff;
      -webkit-transition: none 0s ease 0s;
      transition: none 0s ease 0s;
      -webkit-transition: initial;
      transition: initial;
      width: 230px;
}
.app .sidebar > .el-menu.el-menu--collapse, .app .sidebar > .el-menu.el-menu--collapse:before {
  width: 54px;
}
.app .sidebar > .el-menu > .el-menu-item, .app .sidebar > .el-menu > .el-submenu {
        margin-top: -1px;
        border-top: 1px solid rgb(236, 237, 238);
        border-bottom: 1px solid rgb(236, 237, 238);
}
.app .sidebar > .el-menu .el-submenu .el-menu {
        background-color: rgb(251, 253, 255);
}
.app .sidebar > .el-menu.el-menu--collapse .el-submenu .el-menu {
          -webkit-box-shadow: 5px 5px 5px -5px rgba(0, 0, 0, .2), 5px 5px 5px -5px rgba(0, 0, 0, .2), 5px 5px 5px -5px rgba(0, 0, 0, .2);
                  box-shadow: 5px 5px 5px -5px rgba(0, 0, 0, .2), 5px 5px 5px -5px rgba(0, 0, 0, .2), 5px 5px 5px -5px rgba(0, 0, 0, .2);
          margin-left: 1px;
}
.app .sidebar > .el-menu:not(.el-menu--collapse) .el-submenu .el-menu {
          -webkit-box-shadow: inset 0 4px 4px -2px rgba(0, 0, 0, .15), inset 0 -4px 4px -2px rgba(0, 0, 0, .15);
                  box-shadow: inset 0 4px 4px -2px rgba(0, 0, 0, .15), inset 0 -4px 4px -2px rgba(0, 0, 0, .15);
          background-color: rgb(251, 253, 255);
}
.app .sidebar > .el-menu:not(.el-menu--collapse) .el-submenu .el-menu:before {
  content: "";
  display: block;
  position: absolute;
  z-index: 1;
  left: 25px;
  top: 0;
  bottom: 0;
  border-left: 1px solid rgb(228, 232, 241);
}
.app .sidebar > .el-menu:not(.el-menu--collapse) .el-submenu .el-menu-item:before {
  content: "";
  position: absolute;
  width: 4px;
  height: 4px;
  left: 23px;
  top: 0;
  bottom: 0;
  margin: auto;
  z-index: 2;
  background-color: rgb(251, 253, 255);
  border: 1px solid rgb(228, 232, 241);
}
.app .sidebar > .el-menu:not(.el-menu--collapse) .el-submenu .el-menu-item.is-active:before {
  background-color: #20a0ff;
}
.app .sidebar > .el-menu .el-menu-item:hover, .app .sidebar > .el-menu .el-submenu__title:hover {
        background-color: rgb(238, 241, 246);
}



